import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import residential_img from "./../images/unidad_residential.png";
import residential_mixto from "./../images/unidad_residencial_mixto.png";
import residential_comercial from "./../images/unidad_residencial_comercial.png";
import residential_exito from "./../images/unidad_residential_exito.png";
import residential_public from "./../images/unidad_residencial_public.png";
const ResidentialRegister = () => {
  return (
    <div className="p-4">
      <Row>
        <Col md={7}>
          <Row>
            <Col className="header-3">Mi copropiedad</Col>
          </Row>
          <Row>
            <Col className="header-1 mt-2">Sobre Mi Copropiedad</Col>
          </Row>
        </Col>
        <Col>
          <ul class="steps">
            <li class="step step-success">
              <div class="step-content">
                <span class="step-circle">1</span>
                <span class="step-text">Tipo</span>
              </div>
            </li>
            <li class="step step-active">
              <div class="step-content">
                <span class="step-circle">2</span>
                <span class="step-text">Escritura</span>
              </div>
            </li>
            <li class="step">
              <div class="step-content">
                <span class="step-circle">3</span>
                <span class="step-text">Perfil</span>
              </div>
            </li>
          </ul>
        </Col>
      </Row>

      <Row className="mt-4">
        <Col>
          <Row>
            <Col className="header-5">1. Tipo Coopropiedad </Col>
          </Row>
          <Row>
            <Col className="body-2 mt-3">
              Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam
              nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam
              erat, sed diam voluptua. At vero eos et accusam et justo duo
              dolores et ea rebum. Stet clita kasd gubergren, no sea takimata
              eee
            </Col>
            <Col />
          </Row>

          {/** 
          <Row className="mt-3">
            <Col>
              <div className="unidad-residential-type-img"></div>
            </Col>
            <Col>
              <div className="unidad-residential-type-img"></div>
            </Col>
            <Col>
              <div className="unidad-residential-type-img"></div>
            </Col>
          </Row>
       
       */}

          <Row className="mt-3">
            <Col>
              <div class="grid">
                <label class="card_test">
                  <input class="card__input" type="checkbox" />
                  <div class="card__body text-center">
                    <header class="card__body-header ">
                      <h2 class="card__body-header-title">Residencial</h2>
                    </header>

                    <div class="card__body-cover">
                      <img
                        class="card__body-cover-image"
                        src={residential_img}
                      />
                      <span class="card__body-cover-checkbox">
                        <svg
                          class="card__body-cover-checkbox--svg"
                          viewBox="0 0 12 10"
                        >
                          <polyline points="1.5 6 4.5 9 10.5 1"></polyline>
                        </svg>
                      </span>
                    </div>
                  </div>
                </label>

                <label class="card_test">
                  <input class="card__input" type="checkbox" />
                  <div class="card__body text-center">
                    <header class="card__body-header">
                      <h2 class="card__body-header-title">Mixta</h2>
                    </header>
                    <div class="card__body-cover">
                      <img
                        class="card__body-cover-image"
                        src={residential_mixto}
                      />
                      <span class="card__body-cover-checkbox">
                        <svg
                          class="card__body-cover-checkbox--svg"
                          viewBox="0 0 12 10"
                        >
                          <polyline points="1.5 6 4.5 9 10.5 1"></polyline>
                        </svg>
                      </span>
                    </div>
                  </div>
                </label>

                <label class="card_test">
                  <input class="card__input" type="checkbox" />
                  <div class="card__body text-center">
                    <header class="card__body-header">
                      <h2 class="card__body-header-title">Comercial</h2>
                    </header>
                    <div class="card__body-cover">
                      <img
                        class="card__body-cover-image"
                        src={residential_comercial}
                      />
                      <span class="card__body-cover-checkbox">
                        <svg
                          class="card__body-cover-checkbox--svg"
                          viewBox="0 0 12 10"
                        >
                          <polyline points="1.5 6 4.5 9 10.5 1"></polyline>
                        </svg>
                      </span>
                    </div>
                  </div>
                </label>
              </div>
            </Col>
          </Row>
        </Col>
      </Row>

      <Row className="margin25">
        <Col>
          <Row>
            <Col className="header-5">2. Escritura Publica</Col>
          </Row>
          <Row>
            <div>
              <Col className="body-2 mt-3">
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed
                diam nonumy eirmod tempor invidunt ut labore et dolore magna
              </Col>
            </div>
          </Row>

          <Row className="mt-3">
            <Col>
              <div className="d-flex align-items-center unidad-residential-type-upload-img text-center ">
                <Row>
                  <Col md={12}>
                    <p className="header-5">
                      Arrastra y suelta tu archivo aquí <br /> o cárgalo desde
                      tu explorador
                    </p>
                  </Col>

                  <Col md={12}>
                    <button className="unidad-residencial-btn-upload-img">
                      Adjuntar archivo
                    </button>
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </Col>

        <Col>
          <div className="mt-4 unidad-residential-type-img-2 d-flex align-items-center">
            <img src={residential_public} className="rounded mx-auto d-block" />
          </div>
        </Col>
      </Row>

      <Form>
        <Row className="margin25">
          <Col>
            <Row>
              <Col className="header-5">3. Perfil de la Copropiedad</Col>
            </Row>
            <Row className="mt-3 header-3">
              <Col>
                <Form>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Nombre de la Copropiedad</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="ingrese el nombre coopropiedad"
                    />
                    <Form.Text className="text-muted">
                      Campo necesario por favor ingreselo
                    </Form.Text>
                  </Form.Group>

                  <Form.Row>
                    <Col>
                      <Form.Group controlId="formBasicEmail">
                        <Form.Label>NIT</Form.Label>
                        <Form.Control
                          type="text"
                          placeholder="ingrese el documento del conjunto"
                        />
                        <Form.Text className="text-muted">
                          Campo necesario por favor ingreselo
                        </Form.Text>
                      </Form.Group>
                    </Col>

                    <Col>
                      <Form.Group controlId="formBasicEmail">
                        <Form.Label>Telefono (opcional)</Form.Label>
                        <Form.Control
                          type="text"
                          placeholder="ingrese el documento del conjunto"
                        />
                        <Form.Text className="text-muted">
                          Campo necesario por favor ingreselo
                        </Form.Text>
                      </Form.Group>
                    </Col>
                  </Form.Row>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Ciudad o municipio</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="ingrese Ciudad o municipio"
                    />
                    <Form.Text className="text-muted">
                      Campo necesario por favor ingreselo
                    </Form.Text>
                  </Form.Group>

                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Direccion</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="ingrese Ciudad o municipio"
                    />
                    <Form.Text className="text-muted">Direccion</Form.Text>
                  </Form.Group>

                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Correo electronico</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Correo electronico"
                    />
                    <Form.Text className="text-muted">
                      Correo electronico
                    </Form.Text>
                  </Form.Group>
                </Form>
              </Col>
            </Row>
          </Col>

          <Col>
            <Row>
              <Col className="header-5">4. Responsable legal</Col>
            </Row>
            <Row className="mt-3 header-3">
              <Col>
                <Form.Row>
                  <Col>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Nombres</Form.Label>
                      <Form.Control type="text" placeholder="Ingrese nombres" />
                      <Form.Text className="text-muted">
                        Campo necesario por favor ingreselo
                      </Form.Text>
                    </Form.Group>
                  </Col>

                  <Col>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Apellidos</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="ingrese apellido"
                      />
                      <Form.Text className="text-muted">
                        Campo necesario por favor ingreselo
                      </Form.Text>
                    </Form.Group>
                  </Col>
                </Form.Row>

                <Form.Row>
                  <Col>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Cedula</Form.Label>
                      <Form.Control type="text" placeholder="Ingrese Cedula" />
                      <Form.Text className="text-muted">
                        Campo necesario por favor ingreselo
                      </Form.Text>
                    </Form.Group>
                  </Col>

                  <Col>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Telefono</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="ingrese Telefonono"
                      />
                      <Form.Text className="text-muted">
                        Campo necesario por favor ingreselo
                      </Form.Text>
                    </Form.Group>
                  </Col>
                </Form.Row>

                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Ciudad o municipio</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="ingrese Ciudad o municipio"
                  />
                  <Form.Text className="text-muted">
                    Campo necesario por favor ingreselo
                  </Form.Text>
                </Form.Group>

                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Direccion</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="ingrese Ciudad o municipio"
                  />
                  <Form.Text className="text-muted">Direccion</Form.Text>
                </Form.Group>

                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Correo electronico</Form.Label>
                  <Form.Control type="text" placeholder="Correo electronico" />
                  <Form.Text className="text-muted">
                    Correo electronico
                  </Form.Text>
                </Form.Group>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
      <Row className="margin25 text-center">
        <Col>
          <Row>
            <Col>
              <img
                src={residential_exito}
                className="unidad-residencial-img-sucess"
              ></img>
            </Col>
          </Row>
          <Row>
            <Col md={12}>
              <p className="mt-3 header-5">Registro exitoso</p>
            </Col>

            <Col md={12}>
              <p className="body-2 text-center">
                Ya casi eres parte de veci, <br />
                Espera mientras validamos tu datos.....
                <br /> en breve recibiras una notificacion...
              </p>
            </Col>
          </Row>
        </Col>
      </Row>

      <Row>
        <Col className="align-self-center text-center">siguiente</Col>
      </Row>
    </div>
  );
};

export default ResidentialRegister;
