import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import NavBarScreen from "./NavBarScreen";
import SideBarScreen from "./SideBarScreen";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ResidentialRegister from "../residential/ResidentialRegister";
import { TestPage } from "../residential/TestPage";

export const DasboardScreen = () => {
  return (
    <>
      <Router>
        <Row>
          <Col md={2} className="padding-0">
            <SideBarScreen />
          </Col>
          <Col className="padding-0">
            <Row>
              <Col>
                <NavBarScreen />
              </Col>
            </Row>
            <Row>
              <Col className="dasboardContent padding-0">
                  <Switch>
                    <Route exact path="/" component={ResidentialRegister} />
                    <Route exact path="/about" component={TestPage} />
                    <Route exact path="/login" component={TestPage} />
                    <Redirect to="/" />
                  </Switch>
              </Col>
            </Row>
          </Col>
        </Row>
      </Router>
    </>
  );
};
